import icons from 'url:../../img/icons.svg';
import View from '../views/view.js';

import { Fraction } from 'fractional';

class RenderRecipeView extends View {
  _parentElment = document.querySelector('.recipe');
  data;
  _errorMessage = `錯誤的操作，請重新檢查你的配置`;
  message = '';

  getHandlerUpdataServings(handler) {
    this._parentElment.addEventListener('click', function (e) {
      const btn = e.target.closest('.btn--tiny');
      if (!btn) return;
      const quantity = +btn.dataset.servingss;
      if (quantity <= 0) return;

      handler(quantity);
    });
  }
  getHandlerAddBookMarkde(handler) {
    this._parentElment.addEventListener('click', function (e) {
      const btn = e.target.closest('.btn--bookMark');
      if (!btn) return;
      handler();
    });
  }

  _getStr() {
    console.log(this._data);

    return `
    <figure class="recipe__fig">
      <img src="${this._data.image_url}" alt="${
      this._data.title
    }" class="recipe__img" />
      <h1 class="recipe__title">
        <span>${this._data.title}</span>
      </h1>
    </figure>
  
    <div class="recipe__details">
      <div class="recipe__info">
        <svg class="recipe__info-icon">
          <use href="${icons}.svg#icon-clock"></use>
        </svg>
        <span class="recipe__info-data recipe__info-data--minutes"> ${
          this._data.cooking_time
        }</span>
        <span class="recipe__info-text">minutes</span>
      </div>
      <div class="recipe__info">
        <svg class="recipe__info-icon">
          <use href="${icons}.svg#icon-users"></use>
        </svg>
        <span class="recipe__info-data recipe__info-data--people">${
          this._data.servings
        }</span>
        <span class="recipe__info-text">servings</span>
  
        <div class="recipe__info-buttons">
          <button data-servingss='${
            this._data.servings - 1
          }' class="btn--tiny btn--increase-servings">
            <svg>
              <use href="${icons}#icon-minus-circle"></use>
            </svg>
          </button>
          <button data-servingss='${
            this._data.servings + 1
          }' class="btn--tiny btn--increase-servings">
            <svg>
              <use href="${icons}#icon-plus-circle"></use>
            </svg>
          </button>
        </div>
      </div>
  
      <div class="recipe__user-generated ${this._data.key ? '' : 'hidden'}">
      <svg>
      <use href="${icons}#icon-user"></use>
      </svg>
      </div>

      <button class="btn--round btn--bookMark">
        <svg class="">
          <use href="${icons}.svg#icon-bookmark${
      this._data.bookmarked ? '-fill' : ''
    }">   </use>
        </svg>
      </button>
      
    </div>
  
    <div class="recipe__ingredients">
      <h2 class="heading--2">Recipe ingredients</h2>
      <ul class="recipe__ingredient-list">
  
      ${this._data.ingredients.map(this._settingInGredient).join('')}
  
      </ul>
    </div>
  
    <div class="recipe__directions">
      <h2 class="heading--2">How to cook it</h2>
      <p class="recipe__directions-text">
        This recipe was carefully designed and tested by
        <span class="recipe__publisher">${
          this._data.publisher
        }</span>. Please check out
        directions at their website.
      </p>
      <a
        class="btn--small recipe__btn"
        href="${this._data.source_url}"
        target="_blank"
      >
        <span>Directions</span>
        <svg class="search__icon">
          <use href="${icons}.svg#icon-arrow-right"></use>
        </svg>
      </a>
    </div>
  `;
  }
  _settingInGredient(item) {
    return `
    <li class="recipe__ingredient">
       <svg class="recipe__icon">
         <use href="${icons}.svg#icon-check"></use>
       </svg>
       <div class="recipe__quantity">${
         item.quantity ? new Fraction(item.quantity).toString() : ''
       }</div>
       <div class="recipe__description">
         <span class="recipe__unit">${item.unit}</span>
         ${item.description}
       </div>
     </li>
    `;
  }

  getHandlerRecipe(fn) {
    ['hashchange', 'load'].forEach(even => window.addEventListener(even, fn));
  }
}
const createRecipeView = () => new RenderRecipeView();
export { createRecipeView };
